<template>

	<div id="app">

		<NavBar></NavBar>
    <el-row :gutter="20" style="margin-right:0;margin-left:0px;background:#f4f6f9">
      <el-col :xs="4" :sm="4"  :lg="3" v-bind:style="{width:Width}" style="padding-right:0;position:fixed;padding-left: 0;">
		<LeftBar ></LeftBar></el-col>
                           <!--
                         跟下面命名保持一致
                           -->
      <el-col :xs="24" :sm="24" :lg="21"  v-bind:style="{marginLeft:Left}" style="padding-left: 10px;" >	<router-view    style="padding-top:72px;"></router-view></el-col>
    </el-row>
	</div>
</template>

<script>

	import NavBar from './components/NavBar'
	import LeftBar from './components/LeftBar'

	export default{
		name:'home',
    data(){
		  return {
		    Left:"",
        Width:""
      }
    },
		components:{
			NavBar,
			LeftBar
},
    mounted() {
      if (this._isMobile()) {


        this.Width="74px"
      } else {

        this.Left = "13%"
      }
      ;
    },
    methods:{
      _isMobile() {
        let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
        return flag;
      }

    }

	}
</script>

<style>
</style>



