
<template>
  <div class="table" style="text-align: left;">
    <div class="crumbs" >
      <el-breadcrumb separator-class="el-icon-arrow-right" class="crumb">
        <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>订单信息</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <h3 style="font-weight: 400;">日期： {{Time}}</h3>
      <el-row :gutter="20">
          <el-col :xs="22" :sm="22" :lg="10" :offset="1">
            <div class="card" style="margin-bottom: 20px;">

                <h4 style="color:#99a5bd">总订单数：</h4>
                <h3 style="font-size:1.5rem;font-weight: normal;">{{orders}} 单</h3>
              <img src="../../assets/order.png"/>
            </div>    </el-col>
        <el-col :xs="22" :sm="22" :lg="10" :offset="1">
            <div class="card" style="margin-bottom:20px;">
              <h4 style="color:#99a5bd">消费总额：</h4>
              <h3 style="font-size:1.5rem;font-weight: normal;">￥{{money}} </h3>
              <img src="../../assets/money.png"/>
            </div>
        </el-col>
      </el-row>
      <el-tabs v-model="message">
        <el-tab-pane :label="'全部订单'" name="first">
          <div style="margin-bottom:20px;">
            <el-input v-model="select_word" placeholder="输入用户名筛选" class="handle-input mr10"></el-input>

          </div>
      <div  style="margin-bottom:20px;" class="handle-box">

        <!--<el-button type="primary" style="float: right;margin-right:8%" icon="delete" class="handle-del mr10" @click="delAll">批量删除</el-button>-->

          <div class="block" style="float: left;margin-top: 25px;">
            <span class="demonstration">起始日期</span>
            <el-date-picker
              v-model="startTime"
              align="right"
              type="date"
              v-bind:editable="editabled"
              placeholder="起始日期"
              :picker-options="pickerOptions1">
            </el-date-picker>
          </div>


          <div class="block" style="float: left;margin-top: 25px;">
            <span class="demonstration">截止日期</span>
            <el-date-picker
              v-model="endTime"
              align="right"
              v-bind:editable="editabled"
              type="date"
              placeholder="截止日期"
              :picker-options="pickerOptions2">
            </el-date-picker>
          </div>



        <el-button type="primary" style="margin-top: 25px;"  icon="search" @click="search">搜索</el-button>
        <div style="clear:both"></div>
      </div>
          <el-table v-bind:style="{display:show}" class="mobiltab"
            :data="data"
            style="width: 100%">
            <el-table-column type="expand">
              <template slot-scope="props">
                <el-form label-position="left" inline class="demo-table-expand">
                  <el-form-item label="支付时间">
                    <span>{{ props.row.pay_time }}</span>
                  </el-form-item>
                  <el-form-item label="用户姓名">
                    <span>{{ props.row.user_name }}</span>
                  </el-form-item>
                  <el-form-item label="订单金额">
                    <span>{{ props.row.order_total_money }}</span>
                  </el-form-item>
                  <el-form-item label="参与优惠金额">
                    <span>{{ props.row.order_discount_money }}</span>
                  </el-form-item>
                  <el-form-item label="未参与优惠金额">
                    <span>{{ props.row.order_ndiscount_money }}</span>
                  </el-form-item>
                  <el-form-item label="折扣金额">
                    <span>{{ props.row.discount_money }}</span>
                  </el-form-item>
                  <el-form-item label="优惠券金额">
                    <span>{{ props.row.coupon_money }}</span>
                  </el-form-item>
                  <el-form-item label="实付金额">
                    <span>{{ props.row.money }}</span>
                  </el-form-item>
                  <el-form-item label="订单id">
                    <span>{{ props.row.order_id }}</span>
                  </el-form-item>
                </el-form>
              </template>
            </el-table-column>
            <el-table-column width="150"
              label="支付时间"
              prop="pay_time">
            </el-table-column>
            <el-table-column
              label="用户姓名"
              prop="user_name">
            </el-table-column>
            <el-table-column
              label="订单金额"
              prop="order_total_money">
            </el-table-column>
          </el-table>
      <el-table :data="data" tooltip-effect="dark" border v-bind:style="{width: secWidth,display:hidden}" >


        <el-table-column label="支付时间" prop="pay_time" width="180"  >
        </el-table-column>
        <el-table-column label="用户姓名" prop="user_name" >
        </el-table-column>

        <el-table-column label="订单金额" prop="order_total_money" >
        </el-table-column>
        <el-table-column label="参与优惠"   :show-overflow-tooltip="true" title="订单参与优惠金额" prop="order_discount_money" >
        </el-table-column>
        <el-table-column label="未参与优惠" :show-overflow-tooltip="true" prop="order_ndiscount_money" >
        </el-table-column>
        <el-table-column label="折扣金额" prop="discount_money"  >
        </el-table-column>
        <el-table-column label="优惠券金额" title="优惠券金额" prop="coupon_money" >
        </el-table-column>
        <el-table-column label="实付金额"  prop="money"  >
        </el-table-column>
        <el-table-column label="订单id" title="订单id" width="190" prop="order_id" >

        </el-table-column>

        <el-table-column label="操作" >
          <template slot-scope="scope">
            <el-button size="small" type="primary" @click="handleEdit(scope.$index, scope.row)">详情</el-button>

          </template>
        </el-table-column>
        <el-table-column
          prop="tag"
          label="标签"

          :filters="[{ text: '已支付', value: '已支付' }, { text: '未支付', value: '未支付' }]"
          :filter-method="filterTag"
          filter-placement="bottom-end">
          <template slot-scope="scope">
            <el-tag
              :type="scope.row.tag === '未支付' ? 'danger' : 'success'"
              disable-transitions>{{scope.row.tag}}</el-tag>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination @current-change="handleCurrentChange" layout="prev, pager, next" :total="totalPage">
        </el-pagination>
      </div>
        </el-tab-pane>
        <!--<el-tab-pane :label="`申请退款`" name="second">-->
          <!--<template v-if="message === 'second'">-->
           <!--<div>退款申请</div>-->
          <!--</template>-->
        <!--</el-tab-pane>-->
      </el-tabs>
    </div>

    <!-- 详情弹出框 -->
    <el-dialog title="订单详情" :visible.sync="editVisible" width="90%">
      <el-form ref="delform" :model="delform" label-width="120px">
        <el-form-item  label="订单ID">
          <el-input disabled v-model="delform.order_id"></el-input>
        </el-form-item>



        <el-form-item  label="用户姓名">
          <el-input disabled v-model="delform.user_name"></el-input>
        </el-form-item>


        <el-form-item  label="订单金额">
          <el-input disabled v-model="delform.order_total_money"></el-input>
        </el-form-item>
        <el-form-item  label="参与优惠">
          <el-input disabled v-model="delform.order_discount_money"></el-input>
        </el-form-item>
        <el-form-item  label="未参与优惠">
          <el-input disabled v-model="delform.order_ndiscount_money"></el-input>
        </el-form-item>
        <el-form-item  label="折扣金额">
          <el-input disabled v-model="delform.discount_money"></el-input>
        </el-form-item>
        <el-form-item  label="优惠券金额">
          <el-input disabled v-model="delform.coupon_money"></el-input>
        </el-form-item>
        <el-form-item  label="实付金额">
          <el-input disabled v-model="delform.money"></el-input>
        </el-form-item>
        <el-form-item  label="支付时间">
          <el-input disabled v-model="delform.pay_time"></el-input>
        </el-form-item>
        <el-form-item  label="支付状态">
          <el-input disabled v-model="delform.status"></el-input>
        </el-form-item>
        <div style="clear:both"></div>
      </el-form>
      <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false">取 消</el-button>
                <el-button type="primary" @click="editVisible = false" >确 定</el-button>
            </span>
    </el-dialog>

    <!-- 删除提示框 -->
    <el-dialog title="提示" :visible.sync="delVisible" width="300px" center>
      <div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>
      <span slot="footer" class="dialog-footer">
                <el-button @click="delVisible = false">取 消</el-button>
                <el-button type="primary" @click="deleteRow">确 定</el-button>
            </span>
    </el-dialog>
    <!--<el-button type="primary" @click="test">确 定</el-button>-->
  </div>
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'

  export default {
    name: 'basetable',
    data() {
      return {
        secWidth:"",
        editabled:false,
        money:0,
        message: 'first',//选项卡
        url: './static/vuetable.json',
        tableData: [],
        totalPage:0,
        cur_page: 1,
        Width:"",
       Time:"",
        show:"",
        hidden:"",
        orders:0,
        select_word: '',
        del_list: [],
        is_search: false,
        editVisible: false,
        delVisible: false,
        delform:{
          order_id:"",transaction_id:"",shop_name:"",user_id:"",user_name:"",user_mobile:"",order_total_money:"",order_discount_money:"",order_ndiscount_money:"",
          discount_money:"",coupon_money:"",money:"",pay_time:'',status:""
        },
        form: {

        },
        idx: -1,
        pickerOptions1: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          }
        },
        pickerOptions2: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          }
        },
       startTime:"",
        endTime:""
      }
    },
    created() {
     // this.getData();
      this.getOrderInfo();
    },
    computed: {
      data() {
        return this.tableData.filter((d) => {
        d.tag=d.status;

          let is_del = false;
          for (let i = 0; i < this.del_list.length; i++) {
            if (d.user_name === this.del_list[i].name) {
              is_del = true;
              break;
            }
          }
          if (!is_del) {

            if (d.shop_name.toLowerCase().indexOf(this.select_word) >  -1  ||
              d.user_name.toLowerCase().indexOf(this.select_word) > -1
            ) {
              return d;
            }
          }
        })
      }
    },
    methods: {
      //悬浮提示



      //得到订单
      getOrderInfo(){
        console.log(this.startTime)
        let d=this.startTime;
        let d2=this.endTime;
        let startTime,endTime;
         if(d!=""&&d2!=""&&d!=null&&d2!=null){
          startTime =new Date(d.getFullYear() + '/' + (d.getMonth() + 1) + '/' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds()).valueOf()/1000;

          endTime =new Date( d2.getFullYear() + '/' + (d2.getMonth() + 1) + '/' + d2.getDate() + ' ' + d2.getHours() + ':' + d2.getMinutes() + ':' + d2.getSeconds()).valueOf()/1000;
        }
//用户名得在日期范围内的数据筛选
      //  startTime=Number(this.startTime)
      //  endTime=Number("2017-07-25 02:23:32")
        axios.get(this.$url.getorder,{

          params:{
            token:sessionStorage.getItem("access-token"),
            limit:10,
            page:this.cur_page,
            start_time:startTime,
            end_time:endTime,
            mobile:"",
            name:''
          }
        }).then(
          function (res) {

            this.totalPage=res.data.data.data.total;
           this.orders=res.data.data.info.sum;
           this.money=res.data.data.info.total_money
            if(res.data.data.info.total_money==null){
             this.money=0
            }
            if(res.data.data.start_time==res.data.data.end_time){
             this.Time=res.data.data.start_time;
            }else{
             this.Time=res.data.data.start_time+"     ~     "+res.data.data.end_time
            }

            this.tableData = res.data.data.data.data;

            let data=this.tableData;
            for(let i=0;i<data.length;i++){
              if(data[i].status==2||data[i].status==11||data[i].status==12||data[i].status==-1){//已支付包括退款部分
                this.tableData[i].status="已支付"
              }
              if(data[i].status==0){
                this.tableData[i].status="未支付"
              }
            }

          }.bind(this)  //改变this指向
        ).catch(function (err) {
          console.log(err);
        })

    },
      // 分页导航
      handleCurrentChange(val) {
        console.log(val);
        this.cur_page = val;
        this.getOrderInfo();
      //  this.getData();
      },
      // 获取 easy-mock 的模拟数据
      getData() {
        // 开发环境使用 easy-mock 数据，正式环境使用 json 文件
        if (process.env.NODE_ENV === 'development') {
          // this.url = '/ms/table/list';
        };
        this.$axios.get(this.url, {
          page: this.cur_page
        }).then((res) => {
          this.tableData = res.data.list;
        })
      },
      search() {
        this.is_search = true;
        this.cur_page=1
        if(this.startTime!=""&&this.endTime!=""&&this.startTime!=null&&this.endTime!=null){
          this.getOrderInfo()

        }else{
          if(this.startTime==""){
            this.$message({
              type:"error",
              message:"请选择起始日期"
            })
          }else{
            if(this.endTime==""){
              this.$message({
                type:"error",
                message:"请选择截止日期"
              })
            }else{

                this.$message({
                  type:"error",
                  message:"请选择日期"
                })

            }
          }

        }

      },
      formatter(row, column) {
        return row.address;
      },
      filterTag(value, row) {
        return row.tag === value;
      },
      handleEdit(index, row) {
        this.idx = index;
        const item = this.tableData[index];
        this.delform=item;
        this.editVisible = true;
      },
      handleDelete(index, row) {
        this.idx = index;
        this.delVisible = true;
      },
      filterTag(value, row) { //筛选已支付或未支付
        return row.tag === value;
      },

      notKey:function(){
        console.log('blur');
        this.blur()
      },
      // 保存编辑
      saveEdit() {
        this.$set(this.tableData, this.idx, this.form);
        this.editVisible = false;
        this.$message.success(`修改第 ${this.idx+1} 行成功`);
      },
      // 确定删除
      deleteRow(){
        this.tableData.splice(this.idx, 1);
        this.$message.success('删除成功');
        this.delVisible = false;
      },
      _isMobile() {
        let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
        return flag;
      }
    },
    mounted(){
     // this.getOrderInfo()
      if (this._isMobile()) {
        this.Width="90%"
        this.secWidth = "100%";
        this.show="block";
        this.hidden="none"
      } else {
        this.Width="70%"
        this.secWidth = "92%"
        this.show="none";
        this.hidden="block"
      }
    }
  }

</script>

<style scoped>
  .handle-box {
    margin-bottom: 20px;
  }

  .handle-select {
    width: 120px;
  }
  .container{
    position: relative;
    top:30px}
  .handle-input {
    width: 300px;
    display: inline-block;
  }
  .del-dialog-cnt{
    font-size: 16px;
    text-align: center
  }
  body .el-table th.gutter{
    display: table-cell!important;
  }
  /*.el-form-item{float:left;width:45%;margin-right:2%}*/
  .el-table th>.cell{font-size:14px;font-weight:500;text-align:center}
  .el-picker-panel{left:64px!important;}
.card{height:120px;box-shadow:0 10px 60px 0 rgba(29,29,31,.09);padding:15px;position:relative}
  .card img{position:absolute;right:30px;top:38px;width:28px;}
  .mobiltab .el-form-item{margin-bottom:0}
</style>



