
<template>

<div>
  <div class="crumbs" >
    <el-breadcrumb separator-class="el-icon-arrow-right" class="crumb">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>财务管理</el-breadcrumb-item>
    </el-breadcrumb>
  </div>
    <el-form :model="formData" style="margin-top: 30px;font-size:18px"  ref="formData" label-width="110px" class="demo-formData">
      <el-form-item label="店铺名称: " style="border:0!important;color:black" prop="name">
        <div v-model="formData.name" style="font-size: 18px;">{{formData.name}}</div>
      </el-form-item>
      <el-form-item label="预付款总额: "  prop="total_money">
        <div  style="color:#fc912b;font-size: 18px;" v-model="formData.total_money">{{formData.total_money}} 元</div>
      </el-form-item>

      <el-form-item label="预付款余额: "  prop="money">
        <div style="color:#fc912b;font-size:18px"  v-model="formData.money">{{formData.money}} 元</div>
      </el-form-item>
      <el-form-item label="今日消费总额: "  prop="today_money">
        <div style="color:#fc912b;font-size:18px"  v-model="formData.today_money">{{todaymoney}} 元</div>
      </el-form-item>
    </el-form>

  <div class="table" style="text-align: left;">

    <div class="container">
      <el-tabs v-model="message">
        <el-tab-pane :label="'全部订单'" name="first">

          <div  style="margin-bottom:20px;" class="handle-box">

            <!--<el-button type="primary" style="float: right;margin-right:8%" icon="delete" class="handle-del mr10" @click="delAll">批量删除</el-button>-->

            <div class="block" style="float: left;margin-top: 25px;">
              <span class="demonstration">起始日期</span>
              <el-date-picker
                v-model="startTime"
                align="right"
                type="date"
                v-bind:editable="editabled"
                placeholder="起始日期"
                :picker-options="pickerOptions1">
              </el-date-picker>
            </div>


            <div class="block" style="float: left;margin-top: 25px;">
              <span class="demonstration">截止日期</span>
              <el-date-picker
                v-model="endTime"
                align="right"
                type="date"
                v-bind:editable="editabled"
                placeholder="截止日期"
                :picker-options="pickerOptions2">
              </el-date-picker>
            </div>



            <el-button type="primary" style="margin-top: 25px;"  icon="search" @click="search">搜索</el-button>
            <el-button type="primary" style="margin-top: 25px;"  icon="el-icon-refresh" @click="refresh">显示全部</el-button>
            <div style="clear:both"></div>
          </div>

          <el-table v-bind:style="{display:show}" class="mobiltab"
                    :data="tableData"
                    style="width: 100%">
            <el-table-column type="expand">
              <template slot-scope="props">
                <el-form label-position="left" inline class="demo-table-expand">
                  <el-form-item label="支付时间: ">
                    <span>{{ props.row.created_at }}</span>
                  </el-form-item>
                  <el-form-item label="用户姓名: ">
                    <span>{{ props.row.user_name }}</span>
                  </el-form-item>
                  <el-form-item label="订单金额: ">
                    <span>{{ props.row.money }}</span>
                  </el-form-item>
                  <el-form-item label="类型: ">
                    <span>{{ props.row.status }}</span>
                  </el-form-item>
                  <el-form-item label="订单ID: ">
                    <span>{{ props.row.order_id }}</span>
                  </el-form-item>
                </el-form>
              </template>
            </el-table-column>
            <el-table-column  width="150"
              label="支付时间"
              prop="created_at">
            </el-table-column>
            <el-table-column
              label="订单金额"
              prop="money">
            </el-table-column>
            <el-table-column
              label="用户姓名"
              prop="user_name">
            </el-table-column>

          </el-table>
          <el-table :data="tableData" tooltip-effect="dark" border v-bind:style="{width: secWidth,display:hidden}" >
            <el-table-column label="支付时间" width="180" prop="created_at"  >
            </el-table-column>


            <el-table-column label="订单金额" prop="money" >
            </el-table-column>
            <el-table-column label="用户姓名" prop="user_name" >
            </el-table-column>
            <el-table-column label="类型" prop="status" >
            </el-table-column>
            <el-table-column
              prop="tag"
              label="标签"

              :filters="[{ text: '消费', value: '消费' }, { text: '充值', value: '充值' }]"
              :filter-method="filterTag"
              filter-placement="bottom-end">
              <template slot-scope="scope">
                <el-tag
                  :type="scope.row.status === '充值' ? 'info' : 'success'"
                  disable-transitions>{{scope.row.status}}</el-tag>
              </template>
            </el-table-column>

            <el-table-column label="订单id" width="190" title="订单id" prop="order_id" >

            </el-table-column>



          </el-table>
          <div class="pagination">
            <el-pagination @current-change="handleCurrentChange" layout="prev, pager, next" :total="totalPage">
            </el-pagination>
          </div>
        </el-tab-pane>

      </el-tabs>
    </div>

    <!-- 详情弹出框 -->

  </div>
</div>
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'

  export default {
    name: 'basetable',
    data() {
      return {
        editabled:false,
        message: 'first',//选项卡
        url: './static/vuetable.json',
        tableData: [],
        totalPage:0,
        cur_page: 1,
        show:"",
        hidden:"",
        formData:{},
        secWidth:"",
        del_list: [],
        is_search: false,
        editVisible: false,
        todaymoney:0,

        form: {

        },
        idx: -1,
        pickerOptions1: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          }
        },
        pickerOptions2: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          }
        },
        startTime:"",
        endTime:""
      }
    },
    created() {
      // this.getData();
      this.getMoney();
      this.getOrderInfo();
    },
    computed: {
      data() {
        return this.tableData.filter((d) => {
          d.tag=d.status;

          let is_del = false;
          for (let i = 0; i < this.del_list.length; i++) {
            if (d.user_name === this.del_list[i].name) {
              is_del = true;
              break;
            }
          }
          if (!is_del) {

            if (d.shop_name.toLowerCase().indexOf(this.select_word) >  -1  ||
              d.user_name.toLowerCase().indexOf(this.select_word) > -1
            ) {
              return d;
            }
          }
        })
      }
    },
    methods: {
      //刷新
      refresh:function(){
        location.reload()
      },
      //悬浮提示

      //得到财务信息
      getMoney(){
      axios.get(this.$url.getmoney,{
        params:{
          token:sessionStorage.getItem("access-token")
        }

      }).then(function (res) {
        console.log(res);
        this.formData=res.data.data
      }.bind(this))
      },


      getOrderInfo(){
        console.log(this.startTime)
        var start=this.startTime;
        var end=this.endTime;
        var startTime,endTime;
        if(start!=""&&end!=""&&start!=null&&end!=null){
          startTime =new Date(start.getFullYear() + '/' + (start.getMonth() + 1) + '/' + start.getDate() + ' ' + start.getHours() + ':' + start.getMinutes() + ':' + start.getSeconds()).valueOf()/1000;

          endTime =new Date( end.getFullYear() + '/' + (end.getMonth() + 1) + '/' + end.getDate() + ' ' + end.getHours() + ':' + end.getMinutes() + ':' + end.getSeconds()).valueOf()/1000;
        }
        //用户名得在日期范围内的数据筛选
        //  startTime=Number(this.startTime)
        //  endTime=Number("2017-07-25 02:23:32")
        axios.get(this.$url.getmoney_log,{

          params:{
            token:sessionStorage.getItem("access-token"),
            limit:10,
            page:this.cur_page,
            start_time:startTime,
            end_time:endTime
          }
        }).then(
          function (res) {
            var data=res.data.data.data.data;
            if(res.data.data.info.total_money==null){
              this.todaymoney = 0;
            }else{
                  this.todaymoney=res.data.data.info.total_money
            }


            for(let i=0;i<data.length;i++){
              if(data[i].status==-1){
                data[i].status="消费"
              }else{
                data[i].status="充值"
              }
            }
            this.tableData=res.data.data.data.data
            this.totalPage=res.data.data.data.total;

          }.bind(this)  //改变this指向
        ).catch(function (err) {
          console.log(err);
        })

      },
      // 分页导航
      handleCurrentChange(val) {
        console.log(val);
        this.cur_page = val;
        this.getOrderInfo();
        //  this.getData();
      },
      // 获取 easy-mock 的模拟数据
      getData() {
        // 开发环境使用 easy-mock 数据，正式环境使用 json 文件
          if (process.env.NODE_ENV === 'development') {
            // this.url = '/ms/table/list';
          };
          this.$axios.get(this.url, {
            page: this.cur_page
          }).then((res) => {
            this.tableData = res.data.list;
          })
      },
      search() {
        this.is_search = true;
        this.cur_page=1
        if(this.startTime!=""&&this.endTime!=""&&this.startTime!=null&&this.endTime!=null){
          this.getOrderInfo()
        }else{
          this.$message({
            type:"error",
            message:"请选择日期"
          })
        }

      },
      formatter(row, column) {
        return row.address;
      },
      filterTag(value, row) {
        return row.status === value;
      },
      handleEdit(index, row) {
        this.idx = index;
        const item = this.tableData[index];
        this.delform=item;
        this.editVisible = true;
      },
      handleDelete(index, row) {
        this.idx = index;
        this.delVisible = true;
      },
      filterTag(value, row) { //筛选已支付或未支付
        return row.status === value;
      },


      // 保存编辑
      saveEdit() {
        this.$set(this.tableData, this.idx, this.form);
        this.editVisible = false;
        this.$message.success(`修改第 ${this.idx+1} 行成功`);
      },
      // 确定删除
      deleteRow(){
        this.tableData.splice(this.idx, 1);
        this.$message.success('删除成功');
        this.delVisible = false;
      },
      _isMobile() {
        let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
        return flag;
      }
    },
    mounted(){
      // this.getOrderInfo()
      if (this._isMobile()) {
        this.show="block";
        this.hidden="none"
        this.secWidth = "100%"
      } else {
        this.show="none";
        this.hidden="block"
        this.secWidth = "92%"
      }
    }
  }

</script>

<style scoped>
  .handle-box {
    margin-bottom: 20px;
  }

  .handle-select {
    width: 120px;
  }
  .container{
    position: relative;
    top:30px}
  .handle-input {
    width: 300px;
    display: inline-block;
  }
  .del-dialog-cnt{
    font-size: 16px;
    text-align: center
  }
  body .el-table th.gutter{
    display: table-cell!important;
  }
  .mobiltab .el-form-item{margin-bottom:0}
/*.mobiltab .el-form-item span{color:#fc912b}*/
  .el-table th>.cell{font-size:14px;font-weight:500;text-align:center}
  /*.el-picker-panel{left:64px!important;}*/
</style>



