import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home/Home'
import Orders from '@/views/orders'
import Money from '@/views/Money'
import UpdatePwd from "@/views/password"
const Login = resolve => require(['@/views/Login'], resolve)

Vue.use(Router)

let router=new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login
    }, {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/home',
      name: 'home',
      component: Home,
      redirect:'/welcome',  //系统登陆后重定向到默认首页
      hidden:true,
      children:[{path:'/welcome',	component:()=>import('@/views/shop')},
        {path:'/orders',name:'orders',component:Orders},
        {path:'/money',name:'money',component:Money},
        {path:"/updatepwd",name:"updatepwd",component:UpdatePwd}



      ]
    }
  ]
})
// 访问之前，都检查下是否登录了
router.beforeEach((to, from, next) => {
  // console.log('to:' + to.path)
  if (to.path.startsWith('/login')) {
    window.sessionStorage.removeItem('access-token')
    next()
  } else {
    let token = window.sessionStorage.getItem('access-token')
    if (!token) {
      next({ path: '/login' })
    } else {
      next()
    }
  }
});
//每次请求监测是否token过期
