<template>
	<div class="nav-top">

<div class="line"></div>


    <div class="line"></div>
    <el-menu  v-bind:style="{display:show}"
      :default-active="$router.path"
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      background-color="#545c64"
      text-color="#fff"
      router
      menu-trigger="click"
      active-text-color="#ffd04b">

      <el-submenu index="index">
        <template slot="title"><i style="color:rgb(255, 208, 75)" class="el-icon-menu"></i> </template>
        <el-menu-item index="/welcome">店铺首页</el-menu-item>
        <el-menu-item index="/orders">订单管理</el-menu-item>
        <el-menu-item index="/money">财务管理</el-menu-item>
        <el-menu-item index="/updatepwd">修改密码</el-menu-item>
      </el-submenu>


    </el-menu>
    <!--<div style="color:white;height:100%;line-height:60px;font-size:18px;position: relative;top:-60px;width:60%;margin-left:20%" v-bind:style="{display:show}">{{}}</div>-->
     <img style="width:1.5rem;height:1.5rem;position:absolute;right:10px;top: 15px;margin-right:2%" @click="logout" src="../../../assets/exit.png"/>

	</div>
</template>
<script>
  export default {
    name: 'NavBar',
    data() {
      return {
        activeIndex2: "1",
        show:""
      };
    },
    mounted(){
      if (this._isMobile()) {

        this.show="block"
      } else {

        this.show="none"
      }
    },
    methods: {
      logout: function () {
        this.$router.push("/login")

      },
      _isMobile() {
        let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
        return flag;
      },
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }
</script>


<style>
	body{margin:0;font-weight: 200;}
  .el-menu--horizontal{left:0!important;}
  .el-menu--popup-bottom-start{margin-top:0}
	a{text-decoration: none;}
  .el-menu--horizontal>.el-submenu .el-submenu__icon-arrow{margin-left:-4px;color:rgb(255, 208, 75)}
  .el-menu--horizontal .el-menu .el-menu-item, .el-menu--horizontal .el-menu .el-submenu__title{height:48px;
    line-height: 48px;}
  .nav-top{
    position:fixed;
    background: rgb(84, 92, 100);
    height:60px;
    z-index: 9;width:100%}
	.navtitle{
	position:absolute;left:3%;line-height:60px;margin:0;font-weight: 200;color:white;
	z-index:999}
</style>



