<template>
	<div class="leftBar" v-bind:style="{display:show}" >

	<el-col :span="24" style="padding:0">

    <el-menu style="min-height: 1150px;"
   :default-active="$route.path"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      background-color="#545c64"
      text-color="#fff"
             :collapse="isCollapse"
	     router
      active-text-color="#ffd04b">
       <el-menu-item index="/welcome">
        <i class="el-icon-menu"></i>
        <span slot="title">店铺首页</span>
      </el-menu-item>

      <el-menu-item index="/orders" >
        <i class="el-icon-document"></i>
        <span slot="title">订单管理</span>
      </el-menu-item>
      <el-menu-item index="/money" >
        <i class="el-icon-tickets"></i>
        <span slot="title">财务管理</span>
      </el-menu-item>
      <el-menu-item index="/updatepwd" >
        <i class="el-icon-tickets"></i>
        <span slot="title">修改密码</span>
      </el-menu-item>

    </el-menu>
  </el-col>

	</div>
</template>

<script>
	export default({
		name:'LeftBar',
    data(){
		  return{
        isCollapse:false,
        show:""
      }
    },
    mounted(){
      if (this._isMobile()) {
          this.isCollapse=true
          this.show="none"
      } else {
        this.isCollapse=false
          this.show="block"
      }
    },

		 methods: {
       _isMobile() {
         let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
         return flag;
       },
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      }
    }
	})
</script>

<style>
  body{
    height: 100%;}
  .leftBar{margin-top:61px;height:100%;background:rgb(84, 92, 100);

    text-align: left;}
</style>



