<template>
    <div>
      <el-row :gutter="20" style="height:500px">
        <el-col :xs="20" :sm="20" :offset="2" :lg="12">
          <el-form  :model="formdata" ref="formdata" >
            <el-form-item label="登录账号">
              <el-input disabled v-model="mobile"></el-input>
            </el-form-item>
            <el-form-item label="验证码">
              <el-input  v-model="formdata.code" @blur="resetsend" style="float:left"></el-input>
              <el-button @click="sendMsg" style="float:left;margin-top:8px" type="primary" :disabled="isDisabled">{{buttonName}}</el-button>
            </el-form-item>
            <el-form-item label="新密码">
              <el-input  v-model="formdata.newpwd"></el-input>
            </el-form-item>
            <!--<el-form-item label="确认密码">-->
              <!--<el-input  v-model="newpwd"></el-input>-->
            <!--</el-form-item>-->
            <el-form-item>
              <el-button type="primary" @click="updatePwd">确认修改</el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>


    </div>
</template>

<script>
  import  qs from "qs"
    export default {
        name: "index",
      data(){
          return {
            mobile:"",
            formdata:{},
            buttonName:"发送验证码",
            newpwd:"",
            isDisabled:false,
            time:61,
            timer:""//定时器
          }
      },
      methods: {
        sendMsg() {
          var para={
            token:sessionStorage.getItem("access-token"),
            mobile:sessionStorage.getItem("mobile")
          };
          var params=qs.stringify(para);
          this.$axios.post(this.$url.getcode,
          params).then((res)=>{
            if(res.data.code==401){
              this.$message.error("请传递token");
            }

            this.$message.success("发送成功");
            this.newpwd=this.formdata.newpwd
          })
          let me = this;
          me.isDisabled = true;

         this.timer = window.setInterval(function () {
            me.buttonName = '（' + me.time + '秒）后重新发送';
            --me.time;
            if (me.time < 0) {
              me.buttonName = "重新发送";
              me.time = 61;
              me.isDisabled = false;
              window.clearInterval(this.timer);
            }
          }, 1000);

        },
        updatePwd:function () {
          let para={
            token:sessionStorage.getItem("access-token"),
            mobile:sessionStorage.getItem("mobile"),
            code:this.formdata.code,
            passwd:this.formdata.newpwd
          };
          //axios发送的数据需要是字符串，form-data,所以要序列化，不然报跨域错误？？？？？？？？？？
          let params=qs.stringify(para);
          this.$axios.post(this.$url.updatepwd,params).then((res)=>{
            if(res.data.code==401||res.data.code==400){
              this.$message.error("修改失败")
            }else{
              this.$message.success("修改成功")
            }

          })
        },
        resetsend:function () {
          console.log("blur")
          let me=this;
          me.buttonName = "重新发送";
          me.time = 61;
          me.isDisabled = false;
          window.clearInterval(this.timer);
        }
      },
      mounted(){
          this.mobile=sessionStorage.getItem("mobile")
      }
      }
</script>

<style scoped>

</style>



